import { results } from "./data.js";

const generateResult = (type, container) => {
  const { calculator, extraClass, title } = results[type];

  console.log(calculator);
  container.innerHTML += `
    <div class="result ${extraClass}">
      <h4>${title}</h4>
      <p>1000 mL = 1.0 L</p>
    </div>
  `;
};

export default generateResult;
